    <p class="hint">
       <span class="primary"> span-method </span> <br>
      
    </p>
    <section class="demo">
      <div class="section-content">
        <eff-table v-bind="tableOptions" />
      </div>
    </section>

    <section class="snippets">
      <Collapse>
        <div class="section-content">
          <CodeSnippet class="html" :code="htmlCode" />
          <CodeSnippet class="javascript" :code="jsCode" />
        </div>
      </Collapse>
    </section>
  </div>
</template>

<script>
import CodeSnippet from '../components/CodeSnippet.vue'
import Collapse from '../components/Collapse.vue'

const jsCode = `
  export default {
    data() {
      return {
        tableOptions: {
          maxHeight: 400,
          border: true,
          edit: true,
          copy: true,
          spanMethod: this.colspanMethod,
          columns: [
            {
              show: true,
              prop: 'id',
              title: 'ID'
            },
            {
              show: true,
              prop: 'name',
              title: '',
              edit: true
            },
            {
              show: true,
              prop: 'sex',
              title: '',
              edit: true
            },
            {
              show: true,
              prop: 'phone',
              title: '',
              edit: true
            }
          ],
          data: [
            { id: 1, name: '', sex: '', phone: '13715201314' },
            { id: 2, name: '', sex: '', phone: '13715201314' },
            { id: 3, name: '', sex: '', phone: '13715201314' },
            { id: 4, name: '', sex: '', phone: '13715201314' }
          ]
        }
      }
    },
    methods: {
      colspanMethod({ rowIndex, columnIndex }) {
        if (rowIndex % 2 === 0) {
          if (columnIndex === 1) {
            return { rowspan: 2, colspan: 1 }
          } else if (columnIndex === 2) {
            return { rowspan: 1, colspan: 2 }
          } else if (columnIndex === 3) {
            return { rowspan: 0, colspan: 0 }
          }
        }
      }
    }
  }
  `

const htmlCode = `
  <eff-table v-bind="tableOptions" />
  `
export default {
  name: 'SpanMethod',
  components: {
    CodeSnippet,
    Collapse
  },

  data() {
    return {
      htmlCode,
      jsCode,
      tableOptions: {
        maxHeight: 400,
        border: true,
        edit: true,
        copy: true,
        spanMethod: this.colspanMethod,
        columns: [
          {
            show: true,
            prop: 'id',
            title: 'ID'
          },
          {
            show: true,
            prop: 'name',
            title: '',
            edit: true
          },
          {
            show: true,
            prop: 'sex',
            title: '',
            edit: true
          },
          {
            show: true,
            prop: 'phone',
            title: '',
            edit: true
          }
        ],
        data: [
          { id: 1, name: '', sex: '', phone: '13715201314' },
          { id: 2, name: '', sex: '', phone: '13715201314' },
          { id: 3, name: '', sex: '', phone: '13715201314' },
          { id: 4, name: '', sex: '', phone: '13715201314' }
        ]
      }
    }
  },
  methods: {
    colspanMethod({ rowIndex, columnIndex }) {
      if (rowIndex % 2 === 0) {
        if (columnIndex === 1) {
          return { rowspan: 2, colspan: 1 }
        } else if (columnIndex === 2) {
          return { rowspan: 1, colspan: 2 }
        } else if (columnIndex === 3) {
          return { rowspan: 0, colspan: 0 }
        }
      }
    }
  }
}
