<template>
  <div class="page-home page">
    <h2>Subtotal </h2>
    <p class="hint">
      toolbarConfig  <span class="primary"> subtotal </span>  <span class="primary"> true </span>
      <icon icon="subtotal" /><br>
      row <span class="primary"> subtotal </span> <br>
      <span class="primary"> ctrl +  </span>
    </p>
    <section class="demo">
      <div class="section-content">
        <eff-table
          ref="table"
          v-bind="tableOptions"
        />
      </div>
    </section>

    <section class="snippets">
      <Collapse>
        <div class="section-content">
          <CodeSnippet class="html" :code="htmlCode" />
          <CodeSnippet class="javascript" :code="jsCode" />
        </div>
      </Collapse>
    </section>
  </div>
</template>

<script>
import CodeSnippet from '../components/CodeSnippet.vue'
import Collapse from '../components/Collapse.vue'

const htmlCode = `
  <eff-table
    ref="table"
    v-bind="tableOptions"
  />
  `
const jsCode = `
  
  `
export default {
  name: 'Subtotal',
  components: {
    CodeSnippet,
    Collapse
  },

  data() {
    return {
      htmlCode,
      jsCode,
      tableOptions: {
        maxHeight: 400,
        edit: true,
        border: true,
        toolbarConfig: { subtotal: true },
        columns: [
          {
            show: true,
            prop: 'name',
            title: '',
            titleSuffix: { icon: 'info', message: '' }
          },
          {
            show: true,
            prop: 'number',
            title: ''
          },
          {
            show: true,
            prop: 'email',
            title: ''
          }
        ],
        data: [
          { id: 1, name: '99', number: '12', email: '168@qq.com' },
          { id: 2, name: '888', number: '33', email: '168@qq.com' },
          { id: 5, name: '999999', number: '6', email: '1681681681681' },
          { id: 3, name: '9999', number: '22', email: '168@qq.com' },
          { id: 5, name: '999999', number: '8', email: '1681681681681' },
          { id: 4, name: '888', number: '33', email: '168@qq.com' },
          { id: 5, name: '777', number: '6', email: '1681681681681' }
        ]
      }
    }
  },
  methods: {
    validate() {
      this.$refs.table.validate().then(res => {
        this.$message.success('!')
      }).catch(data => {
        this.$message.error('!')
        // console.log(JSON.stringify(data, null, 2))
      })
    },
    fullValidate() {
      this.$refs.table.validate(true).then(res => {
        this.$message.success('!')
      }).catch(data => {
        this.$message.error('!')
        // console.log(JSON.stringify(data, null, 2))
      })
    },
    validateChecked() {
      const rows = this.$refs.table.getCheckRows()
      if (rows.length) {
        this.$refs.table.validate(rows).then(res => {
          this.$message.success('!')
        }).catch(data => {
          this.$message.error('!')
          // console.log(JSON.stringify(data, null, 2))
        })
      } else {
        this.$message.error('!')
      }
    },
    clearValidate() {
      this.$refs.table.clearValidate()
    }
  }
}
</script>
